import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Form, Button } from 'react-bootstrap';
import './addUser.css';

const AddUser = ({ label, currentUser, addUser, handleClose }) => {

    const initialFormState = { id: null, firstName: '', lastName: '', email: '', mobile: '', password: '' };
    const formIsValidation = {
        firstName: {
            isError: false,
            errorMessage: ''
        },
        lastName: {
            isError: false,
            errorMessage: ''
        },
        email: {
            isError: false,
            errorMessage: ''
        },
        mobile: {
            isError: false,
            errorMessage: ''
        },
        password: {
            isError: false,
            errorMessage: ''
        }
    }

    const formIsNotValidation = {
        firstName: {
            isError: true,
            errorMessage: 'firstName is required'
        },
        lastName: {
            isError: true,
            errorMessage: 'lastName is required'
        },
        email: {
            isError: true,
            errorMessage: 'email is required'
        },
        mobile: {
            isError: true,
            errorMessage: 'mobile is required'
        },
        password: {
            isError: true,
            errorMessage: 'password is required'
        }
    }
    const [user, setUser] = useState(initialFormState);
    const [userForm, setUserForm] = useState(formIsValidation);

    const handleInputChange = event => {
        const { name, value } = event.target;
        setUser({ ...user, [name]: value })
    }


    const handleUserSubmit = (event) => {
        let arr = [];
        let isValid = false;
        event.preventDefault();
        if (!user.firstName && !user.lastName && !user.email && !user.mobile && !user.password) {
            setUserForm(formIsNotValidation);
            isValid = false;
            return;
        } else {
            checkValidation(user);
        }

        for (let key in user) {
            if (key !== 'id') {
                if (userForm[key].isError === true) {
                    arr.push(userForm[key].isError)
                }
                isValid = arr.length ? false : true;
            }
        }

        if (isValid) {
            addUser(user, label);
            setUser(initialFormState);
            handleClose();
        }
    }

    const checkValidation = (user) => {
        let emailPattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);

        let mobilePattern = new RegExp(/^[0-9\b]+$/);

        for (let key in user) {
            if (key !== 'id' && user[key] === '') {
                userForm[key].isError = true;
                userForm[key].errorMessage =`${key} is required`;
            }
            
            if (key !== 'id' && user[key] !== '') {
                userForm[key].isError = false;
                userForm[key].errorMessage ='';
            }

            if (key === 'email' && user['email'] !== '' && !emailPattern.test(user['email'])) {
                userForm.email.isError = true;
                userForm.email.errorMessage ='Please enter valid email address.';
            }

            if (key === 'mobile' && user['mobile'] !== '' && !mobilePattern.test(user['mobile'])) {
                userForm.mobile.isError = true;
                userForm.mobile.errorMessage ='Please enter only number.';
            }

            if (key === 'mobile' && user['mobile'] !== '' && user['mobile'].length !== 10) {
                userForm.mobile.isError = true;
                userForm.mobile.errorMessage ='Mobile number not be greater than 10';
            }
        }
        let userUpdate = {...userForm};
        setUserForm(userUpdate);
        return;
    }

    useEffect(
        () => {
            setUser(currentUser)
        },
        [currentUser]
    )

    return (
        <>
            <Form onSubmit={handleUserSubmit}>
                <Form.Group controlId="formBasicFname">
                    <Form.Label>FirstName</Form.Label>
                    <Form.Control type="text" placeholder="Enter FirstName" name='firstName' value={user.firstName} onChange={handleInputChange} className={`${userForm.firstName.isError ? 'shown-border' : ''}`}/>
                </Form.Group>
                {userForm.firstName.isError && <p className="errorMessage">{userForm.firstName.errorMessage}</p>}

                <Form.Group controlId="formBasicLname">
                    <Form.Label>LastName</Form.Label>
                    <Form.Control type="text" placeholder="Enter LastName" name='lastName' value={user.lastName} onChange={handleInputChange} className={`${userForm.lastName.isError ? 'shown-border' : ''}`}/>
                </Form.Group>
                {userForm.lastName.isError && <p className="errorMessage">{userForm.lastName.errorMessage}</p>}

                <Form.Group controlId="formBasicEmail">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" name='email' value={user.email} onChange={handleInputChange} className={`${userForm.email.isError ? 'shown-border' : ''}`}/>
                </Form.Group>
                {userForm.email.isError && <p className="errorMessage">{userForm.email.errorMessage}</p>}

                <Form.Group controlId="formBasicMobile">
                    <Form.Label>Email Mobile</Form.Label>
                    <Form.Control type="text" placeholder="Enter Mobile" name='mobile' value={user.mobile} onChange={handleInputChange} className={`${userForm.mobile.isError ? 'shown-border' : ''}`}/>
                </Form.Group>
                {userForm.mobile.isError && <p className="errorMessage">{userForm.mobile.errorMessage}</p>}

                <Form.Group controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" name='password' value={user.password} onChange={handleInputChange} className={`${userForm.password.isError ? 'shown-border' : ''}`}/>
                </Form.Group>
                {userForm.password.isError && <p className="errorMessage">{userForm.password.errorMessage}</p>}

                {label === 'ADD_USER' ? (
                    <Button variant="primary" type="submit">Add New User</Button>
                ) : (
                    <Button variant="primary" type="submit">Update User</Button>
                )}
            </Form>
        </>
    )
}

export default AddUser;